$footerColor: #A3A3A3;
$footerBackground: #000000;
$footerBottomColor: #FFFFFF;
$footerBottomBackground: #575757;
$navColor: #B0BBC9;
$navBackgroundColor: #222538;
$c1: #2d3666;
$c2: $navBackgroundColor /* header background color*/;
$c3: #4698ff;
$c4: #f4f4f4;

@mixin colors($color, $class) {
    .#{$class} {
        color: $color; }
    .#{$class}-bg {
        background-color: $color; }
    .#{$class}-bo {
        border-color: $color !important; } }


@include colors($c1, c1);
@include colors($c2, c2);
@include colors($c3, c3);

.trns {
    -webkit-transition: all 0.3s cubic-bezier(0.84, 0.35, 0.39, 0.74) 0s;
    transition: all 0.3s cubic-bezier(0.84, 0.35, 0.39, 0.74) 0s; }
